// src/core/BaseControl.ts
export abstract class BaseControl extends HTMLElement {
  protected shadow: ShadowRoot;

  constructor() {
    super();
    this.shadow = this.attachShadow({ mode: 'open' });
  }

  connectedCallback() {
    this.render();
    this.attachEventListeners();
  }

  disconnectedCallback() {
    this.detachEventListeners();
  }

  static get observedAttributes(): string[] {
    return [];
  }

  attributeChangedCallback(name: string, oldValue: string, newValue: string) {
    if (oldValue !== newValue) {
      const prop = this.attributeToProperty(name);
      if (prop && (this as any)[prop] !== newValue) {
        (this as any)[prop] = newValue;
      }
    }
  }

  protected attributeToProperty(attr: string): string | null {
    return null; // override in subclasses
  }

  protected abstract render(): void;

  protected attachEventListeners(): void {}
  protected detachEventListeners(): void {}
}
